import React from 'react'
import '../css/backbutton.css' 

class BackButton extends React.Component {
  handleClick() {
    window.location = '/'
  }

  render() {
    return (
      <div class='buttonContainer' onClick={this.handleClick.bind(this)}>
        <img class='backButton' src='backarrow.svg'/>
      </div>
    )
  }
}

export default BackButton